.moonclick{
  height: 35px;
  width: 35px;
}
.moonclick:hover{
  height: 35px;
  width: 35px;
  animation: moonclick 2s ;
  animation: moonclick 2s infinite;
}
@keyframes moonclick
 {
  0%{
    transform: rotate(0deg);
  }  
  25%{
    transform: rotate(72deg);
  } 
  50%{
    transform: rotate(144deg);
  } 
  75%{
    transform: rotate(216deg);
  } 
  100%{
    transform: rotate(360deg);
  } 
  
}
